import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const YearNationStackedChart = () => {
    const chartRef = useRef();
    const [stackedData, setStackedData] = useState(null);

    useEffect(() => {
        // 1) JSON 
        d3.json('/df_month.json').then(rawData => {
            if (!rawData || !Array.isArray(rawData)) {
                console.error('df_month.json is not valid array data');
                return;
            }
            //    
            console.log('Data sample:', rawData[0]);

            // 2) (, ) => 
            // grouped: [ [, [ [, count], [, count] ] ], ... ]
            const grouped = d3.rollups(
                rawData,
                v => v.length,
                d => d[''],    //  
                d => d['']     //  
            );

            //  /   grouped 
            if (!grouped.length) {
                console.error('No (, ) data found');
                return;
            }

            // 3)  ,   
            const yearSet = new Set();
            grouped.forEach(([nation, arr]) => {
                arr.forEach(([year]) => {
                    yearSet.add(year);
                });
            });
            const allYears = Array.from(yearSet).filter(y => y != null).sort();
            const allNations = grouped.map(([n]) => n).filter(n => n != null).sort();

            if (!allYears.length || !allNations.length) {
                console.error('No valid years or nations found');
                return;
            }

            // 4)    { nation, 2022:xx, 2023:xx, ... } 
            // fillna(0)
            const finalData = grouped.map(([nation, arr]) => {
                const obj = { nation };
                arr.forEach(([year, count]) => {
                    obj[year] = count;
                });
                allYears.forEach(y => {
                    if (!obj[y]) obj[y] = 0;
                });
                return obj;
            });

            setStackedData({
                data: finalData,
                years: allYears
            });
        });
    }, []);

    useEffect(() => {
        // stackedData    
        if (!stackedData) return;
        const { data, years } = stackedData;
        if (!Array.isArray(data) || !Array.isArray(years) || !data.length || !years.length) {
            console.warn('StackedData is missing or empty');
            return;
        }

        //  
        const svgWidth = 1000;
        const svgHeight = 500;
        const margin = { top: 50, right: 150, bottom: 100, left: 70 };

        //   
        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X: 
        const xScale = d3.scaleBand()
            .domain(data.map(d => d.nation))
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        //  key: 
        const stackGen = d3.stack()
            .keys(years)
            .order(d3.stackOrderNone)
            .offset(d3.stackOffsetNone);

        //  
        const series = stackGen(data);
        // series => [ Array(  ), ...], key = 

        // Y ()
        const maxY = d3.max(data, d => {
            let sum = 0;
            years.forEach(y => sum += d[y]);
            return sum;
        });
        const yScale = d3.scaleLinear()
            .domain([0, maxY])
            .nice()
            .range([svgHeight - margin.bottom, margin.top]);

        // Viridis 
        //   [0..1]  
        const color = d3.scaleOrdinal(
            years.map((year, i) => d3.interpolateViridis(
                years.length === 1 ? 0.5 : i / (years.length - 1)
            ))
        ).domain(years);

        //  
        svg.selectAll('.layer')
            .data(series)
            .enter()
            .append('g')
            .attr('class', 'layer')
            .attr('fill', d => color(d.key))
            .selectAll('rect')
            .data(d => d)
            .enter()
            .append('rect')
            .attr('x', d => xScale(d.data.nation))
            .attr('y', d => yScale(d[1]))
            .attr('height', d => yScale(d[0]) - yScale(d[1]))
            .attr('width', xScale.bandwidth());

        // X
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .attr('transform', 'rotate(-45)')
            .style('text-anchor', 'end')
            .style('font-size', '12px');

        // Y
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale))
            .selectAll('text')
            .style('font-size', '12px');

        // 
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .text('   ');

        // X 
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', svgHeight - 5)
            .attr('text-anchor', 'middle')
            .style('font-size', '14px')
            .text('');

        // Y 
        svg.append('text')
            .attr('transform', 'rotate(-90)')
            .attr('x', -(svgHeight / 2))
            .attr('y', margin.left - 50)
            .attr('text-anchor', 'middle')
            .style('font-size', '14px')
            .text('');

        //  ()
        const legend = svg.append('g')
            .attr('transform', `translate(${svgWidth - margin.right + 20}, ${margin.top})`);

        years.forEach((year, i) => {
            const row = legend.append('g')
                .attr('transform', `translate(0, ${i * 20})`);

            row.append('rect')
                .attr('width', 15)
                .attr('height', 15)
                .attr('fill', color(year));

            row.append('text')
                .attr('x', 20)
                .attr('y', 12)
                .style('font-size', '12px')
                .text(year);
        });

    }, [stackedData]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default YearNationStackedChart;
