import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

/**
 *     (p-value   )
 */
function pearsonCorrelation(xArray, yArray) {
    if (xArray.length !== yArray.length) return null;
    const n = xArray.length;
    const meanX = d3.mean(xArray);
    const meanY = d3.mean(yArray);
    let num = 0;
    let den1 = 0;
    let den2 = 0;
    for (let i=0; i<n; i++){
        const dx = xArray[i] - meanX;
        const dy = yArray[i] - meanY;
        num += dx * dy;
        den1 += dx * dx;
        den2 += dy * dy;
    }
    const corr = num / Math.sqrt(den1 * den2);
    return corr;
}

/**
 *  (OLS) (x, y) => { slope, intercept }
 */
function linearRegression(data, xKey, yKey) {
    const n = data.length;
    if (n === 0) return { slope: 0, intercept: 0 };

    const meanX = d3.mean(data, d => d[xKey]);
    const meanY = d3.mean(data, d => d[yKey]);
    let num = 0, den = 0;
    data.forEach(d => {
        const dx = d[xKey] - meanX;
        num += dx * (d[yKey] - meanY);
        den += dx * dx;
    });
    const slope = den === 0 ? 0 : num / den;
    const intercept = meanY - slope * meanX;
    return { slope, intercept };
}

const SatisfactionCorrelationChart = () => {
    const chartRef = useRef();
    const [scatterData, setScatterData] = useState([]);

    //   ()
    const [correlation, setCorrelation] = useState(null);

    useEffect(() => {
        // JSON  (: merged_satisfaction_df2.json)
        d3.json('/merged_satisfaction_df2.json').then(data => {
            if (!data || !Array.isArray(data)) {
                console.error("merged_satisfaction_df2.json is invalid or not array");
                return;
            }
            //  
            const cleaned = data.map(d => ({
                "": d[""],
                "  ": +d["  "],
                " ": +d[" "]
            }))
                .filter(d => !isNaN(d["  "]) && !isNaN(d[" "]));

            //  
            const xArray = cleaned.map(d => d["  "]);
            const yArray = cleaned.map(d => d[" "]);
            const r = pearsonCorrelation(xArray, yArray);

            setScatterData(cleaned);
            setCorrelation(r);
        });
    }, []);

    useEffect(() => {
        if (!scatterData.length) return;

        const svgWidth = 800;
        const svgHeight = 600;
        const margin = { top: 60, right: 30, bottom: 70, left: 80 };

        //   
        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X, Y  
        const xMin = d3.min(scatterData, d => d["  "]) || 0;
        const xMax = d3.max(scatterData, d => d["  "]) || 1;
        const yMin = d3.min(scatterData, d => d[" "]) || 0;
        const yMax = d3.max(scatterData, d => d[" "]) || 1;

        const xScale = d3.scaleLinear()
            .domain([0, xMax * 1.1])  // 
            .range([margin.left, svgWidth - margin.right]);

        const yScale = d3.scaleLinear()
            .domain([0, yMax * 1.1])
            .range([svgHeight - margin.bottom, margin.top]);

        //  
        const { slope, intercept } = linearRegression(scatterData, "  ", " ");

        // 
        svg.selectAll('circle.point')
            .data(scatterData)
            .enter()
            .append('circle')
            .attr('class','point')
            .attr('cx', d => xScale(d["  "]))
            .attr('cy', d => yScale(d[" "]))
            .attr('r', 5)
            .attr('fill', 'steelblue')
            .attr('opacity', 0.7);

        //  path
        //  xRange -> y=slope*x+intercept
        const lineX1 = xMin * 0.9;  // or 0
        const lineX2 = xMax * 1.1;
        const lineY1 = slope * lineX1 + intercept;
        const lineY2 = slope * lineX2 + intercept;

        const lineGenerator = d3.line()
            .x(d => xScale(d.x))
            .y(d => yScale(d.y));

        const regressionData = [
            { x: lineX1, y: lineY1 },
            { x: lineX2, y: lineY2 }
        ];

        svg.append('path')
            .datum(regressionData)
            .attr('fill','none')
            .attr('stroke','red')
            .attr('stroke-width',2)
            .attr('d', lineGenerator);

        // X
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale));

        // Y
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale));

        // Title
        svg.append('text')
            .attr('x', svgWidth/2)
            .attr('y', margin.top/2)
            .attr('text-anchor','middle')
            .style('font-size','16px')
            .text('Correlation: Average Tour Size vs. Satisfaction Rate (with Regression)');

        // X label
        svg.append('text')
            .attr('x', svgWidth/2)
            .attr('y', svgHeight - 20)
            .attr('text-anchor','middle')
            .style('font-size','14px')
            .text('  ');

        // Y label
        svg.append('text')
            .attr('transform','rotate(-90)')
            .attr('x', -svgHeight/2)
            .attr('y', margin.left - 50)
            .attr('text-anchor','middle')
            .style('font-size','14px')
            .text(' ');

        //  ()
        // correlation( r)  setCorrelation state  
        // p-value     or   
    }, [scatterData]);

    return (
        <div>
            {/*    */}
            {Number.isFinite(correlation) && (
                <div style={{ marginBottom: "10px", fontSize: "14px", color: "gray" }}>
                    Pearson r = {correlation.toFixed(3)}
                </div>
            )}
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default SatisfactionCorrelationChart;
