import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const AgeGenderMonthStackedChart = () => {
    const chartRef = useRef();
    const [stackedData, setStackedData] = useState(null);

    useEffect(() => {
        d3.json('/df_month.json').then(data => {
            // (, , ) => count
            //  (, ) groupby ,  columns  stacked
            // => data : [ { month, gender, 1: count, 2: count, ... }, ... ]

            // 1) rollups( (, ),  => count )
            const grouped = d3.rollups(
                data,
                v => d3.rollups(
                    v,
                    vv => vv.length,
                    d => d['']
                ),
                d => d[''],
                d => d['']
            );
            // grouped :
            // [ [, [ [, [ [, count], [, count] ] ], ... ] ], ... ]

            // 2)   
            const allAgesSet = new Set();
            grouped.forEach(([month, genderArr]) => {
                genderArr.forEach(([gender, ageArr]) => {
                    ageArr.forEach(([age]) => {
                        allAgesSet.add(age);
                    });
                });
            });
            const allAges = Array.from(allAgesSet);

            // 3)   => [{ month, gender, 1:xx, 2:xx }, ...]
            const finalData = [];
            grouped.forEach(([month, genderArr]) => {
                genderArr.forEach(([gender, ageArr]) => {
                    const obj = { month: +month, gender };
                    // ageArr => [ [, count], ... ]
                    ageArr.forEach(([age, count]) => {
                        obj[age] = count;
                    });
                    //   0
                    allAges.forEach(a => {
                        if (!obj[a]) obj[a] = 0;
                    });
                    finalData.push(obj);
                });
            });
            // ,  
            finalData.sort((a,b) => a.month - b.month || a.gender.localeCompare(b.gender));

            setStackedData({ data: finalData, ages: allAges });
        });
    }, []);

    useEffect(() => {
        if (!stackedData) return;

        const { data, ages } = stackedData;

        const svgWidth = 900;
        const svgHeight = 500;
        const margin = { top: 50, right: 150, bottom: 100, left: 70 };

        d3.select(chartRef.current).selectAll('*').remove();
        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X0: (month, gender)  
        //   => : "3-", "3-", "4-", "4-", ...
        const xDomain = data.map(d => `${d.month}-${d.gender}`);
        const xScale = d3.scaleBand()
            .domain(xDomain)
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        //  
        const stackGenerator = d3.stack()
            .keys(ages)  //  
            .order(d3.stackOrderNone)
            .offset(d3.stackOffsetNone);

        const series = stackGenerator(data);

        // Y 
        const maxY = d3.max(data, d => {
            let sum = 0;
            ages.forEach(age => {
                sum += d[age];
            });
            return sum;
        });
        const yScale = d3.scaleLinear()
            .domain([0, maxY])
            .range([svgHeight - margin.bottom, margin.top]);

        //  (viridis)
        const color = d3.scaleSequential()
            .domain([0, ages.length - 1])
            .interpolator(d3.interpolateViridis);

        // Stacked rect
        svg.selectAll('g.layer')
            .data(series)
            .enter()
            .append('g')
            .attr('fill', (d, i) => color(i))
            .selectAll('rect')
            .data(d => d)
            .enter()
            .append('rect')
            .attr('x', d => xScale(`${d.data.month}-${d.data.gender}`))
            .attr('y', d => yScale(d[1]))
            .attr('height', d => yScale(d[0]) - yScale(d[1]))
            .attr('width', xScale.bandwidth());

        // X
        svg.append('g')
            .attr('transform', `translate(0, ${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .attr('transform', 'rotate(-45)')
            .style('text-anchor', 'end')
            .style('font-size', '12px')
            .text(d => {
                //  : "3-" => "3-"
                const [m, g] = d.split('-');
                return `${m}-${g}`;
            });

        // Y
        svg.append('g')
            .attr('transform', `translate(${margin.left}, 0)`)
            .call(d3.axisLeft(yScale));

        // Title
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .text(',     ');

        //  ()
        const legend = svg.append('g')
            .attr('transform', `translate(${svgWidth - margin.right + 20}, ${margin.top})`);

        ages.forEach((age, i) => {
            const row = legend.append('g')
                .attr('transform', `translate(0, ${i * 20})`);

            row.append('rect')
                .attr('width', 15)
                .attr('height', 15)
                .attr('fill', color(i));

            row.append('text')
                .attr('x', 20)
                .attr('y', 12)
                .style('font-size', '12px')
                .text(age);
        });

    }, [stackedData]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default AgeGenderMonthStackedChart;
