import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const ContinentAgeHeatmap = () => {
    const chartRef = useRef();
    const [matrixData, setMatrixData] = useState([]);
    const [allContinents, setAllContinents] = useState([]);
    const [allAges, setAllAges] = useState([]);

    // (1)    / 
    useEffect(() => {
        d3.json('/tour_gender_data.json').then(data => {
            // 1)    
            const continentMapping = {
                "": "North America",
                "": "Europe",
                "": "Asia",
                "": "North America",
                " ": "Asia",
                "": "Asia",
                "": "Asia",
                "": "Europe",
                " ": "Africa",
                "": "Asia",
                "": "Europe",
                "": "Asia",
                "": "Asia",
                "": "Asia",
                "": "Europe",
                "": "South America",
                "": "South America",
                "": "Europe",
                "": "Asia",
                "": "Europe",
                " & ": "Oceania",
                "": "Asia",
                "": "Asia",
                "": "Asia",
                "": "Europe",
                "": "Europe",
                "": "Asia",
                "": "Europe",
                "": "Asia",
                "": "Asia",
                "": "Oceania",
                "": "Europe",
                "": "Europe",
                "": "Europe",
                "": "Europe",
                "": "Oceania",
                "": "Asia",
                "": "South America",
                "": "Europe",
                "": "Asia",
                "": "Europe",
                "": "Asia",
                "": "Asia",
                "": "Asia",
                "": "Europe",
                "": "Europe",
                "": "Asia",
                "": "Asia",
                "": "Europe",
                "": "Europe",
                "": "Asia",
                "": "South America",
                "": "Europe",
                "": "South America",
            };

            // 2) (, )    
            //    continent = continentMapping[] or "Others" if missing
            const grouped = d3.rollups(
                data,
                v => d3.sum(v, d => d['  ']),
                d => {
                    const c = continentMapping[d['']];
                    return c ? c : 'Others';
                },
                d => d['']
            );
            // grouped :
            // [
            //   ["Asia", [ ["early 20s", ], ["30s", ], ... ] ],
            //   ["Europe", [ ["early 20s", ], ... ] ],
            //   ...
            // ]

            // 3) ,   
            const continentSet = new Set();
            const ageSet = new Set();
            grouped.forEach(([continent, arr]) => {
                continentSet.add(continent);
                arr.forEach(([age]) => ageSet.add(age));
            });

            const allContinentsArr = Array.from(continentSet).sort();
            const allAgesArr = Array.from(ageSet).sort();

            // 4) Heatmap  
            //    => [{ continent, age, count }, ...]
            const finalMatrix = [];
            grouped.forEach(([continent, ageArr]) => {
                const ageMap = new Map(ageArr);
                allAgesArr.forEach(ageKey => {
                    const val = ageMap.get(ageKey) || 0;
                    finalMatrix.push({
                        continent,
                        age: ageKey,
                        count: val
                    });
                });
            });

            //  
            setAllContinents(allContinentsArr);
            setAllAges(allAgesArr);
            setMatrixData(finalMatrix);
        });
    }, []);

    // (2) D3  
    useEffect(() => {
        if (matrixData.length === 0) return;

        // Dimensions
        const svgWidth = 900;
        const svgHeight = 600;
        const margin = { top: 50, right: 50, bottom: 100, left: 130 };

        //   
        d3.select(chartRef.current).selectAll('*').remove();

        // SVG 
        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X = 
        const xScale = d3.scaleBand()
            .domain(allAges)
            .range([margin.left, svgWidth - margin.right])
            .padding(0.05);

        // Y = 
        const yScale = d3.scaleBand()
            .domain(allContinents)
            .range([margin.top, svgHeight - margin.bottom])
            .padding(0.05);

        //  
        const maxVal = d3.max(matrixData, d => d.count);
        const colorScale = d3.scaleSequential()
            .domain([0, maxVal || 1])
            .interpolator(d3.interpolateYlGnBu);

        // X
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .style('font-size', '12px')
            .style('text-anchor', 'middle')
            .attr('dy', '1em');

        // Y
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale))
            .selectAll('text')
            .style('font-size', '12px');

        // rect 
        svg.selectAll('rect.tile')
            .data(matrixData)
            .enter()
            .append('rect')
            .attr('class', 'tile')
            .attr('x', d => xScale(d.age))
            .attr('y', d => yScale(d.continent))
            .attr('width', xScale.bandwidth())
            .attr('height', yScale.bandwidth())
            .attr('fill', d => colorScale(d.count));

        //     
        svg.selectAll('text.value')
            .data(matrixData)
            .enter()
            .append('text')
            .attr('class', 'value')
            .attr('x', d => xScale(d.age) + xScale.bandwidth() / 2)
            .attr('y', d => yScale(d.continent) + yScale.bandwidth() / 1.6)
            .attr('text-anchor', 'middle')
            .attr('font-size', '12px')
            .attr('fill', 'black')
            .text(d => d.count === 0 ? '' : d.count);

        // Title
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .attr('font-size', '16px')
            .text('Heatmap of Tour Visits by Continent and Age Group');

        // X label
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', svgHeight - 10)
            .attr('text-anchor', 'middle')
            .attr('font-size', '14px')
            .text('Age Group');

        // Y label
        svg.append('text')
            .attr('transform', 'rotate(-90)')
            .attr('x', -svgHeight / 2)
            .attr('y', 20)
            .attr('text-anchor', 'middle')
            .attr('font-size', '14px')
            .text('Continent');

    }, [matrixData, allAges, allContinents]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default ContinentAgeHeatmap;
