import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const TourBubbleChart = () => {
    const chartRef = useRef();
    const [data, setData] = useState([]);

    useEffect(() => {
        // 1) JSON 
        // ) [
        //   {
        //     "": "",
        //     "  ": 8.7,
        //     " ": 0.9,
        //     " ": 21.0
        //   },
        //   ...
        // ]
        d3.json('/merged_satisfaction_df2.json').then(raw => {
            if (!raw || !Array.isArray(raw)) {
                console.error("merged_satisfaction_df2.json is invalid or not array");
                return;
            }
            //  
            const cleaned = raw.map(d => ({
                ...d,
                "  ": +d["  "],
                " ": +d[" "],
                " ": +d[" "]
            }));
            setData(cleaned);
        });
    }, []);

    useEffect(() => {
        if (!data.length) return;

        //   
        const svgWidth = 800;
        const svgHeight = 600;
        const margin = { top: 50, right: 50, bottom: 70, left: 70 };

        //   
        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X, Y  
        const xMin = d3.min(data, d => d["  "]) ?? 0;
        const xMax = d3.max(data, d => d["  "]) ?? 10;
        const yMin = d3.min(data, d => d[" "]) ?? 0;
        const yMax = d3.max(data, d => d[" "]) ?? 1;

        // X :   
        const xScale = d3.scaleLinear()
            .domain([0, xMax * 1.1]) //  
            .range([margin.left, svgWidth - margin.right]);

        // Y :  
        const yScale = d3.scaleLinear()
            .domain([0, yMax * 1.1])
            .range([svgHeight - margin.bottom, margin.top]);

        //  :  
        const rMin = 5;
        const rMax = 25;
        const respondersMin = d3.min(data, d => d[" "]) ?? 1;
        const respondersMax = d3.max(data, d => d[" "]) ?? 1;

        const rScale = d3.scaleSqrt()
            .domain([respondersMin, respondersMax])
            .range([rMin, rMax]);

        // :   interpolateBlues
        //  ? or ?       
        const colorScale = d3.scaleSequential()
            .domain([0, xMax]) // x   
            .interpolator(d3.interpolateBlues);

        //  div
        const tooltip = d3.select("body").append("div")
            .style("position","absolute")
            .style("padding","8px")
            .style("border","1px solid #ccc")
            .style("border-radius","4px")
            .style("background","#fff")
            .style("pointer-events","none")
            .style("opacity",0);

        // ()
        svg.selectAll("circle")
            .data(data)
            .enter()
            .append("circle")
            .attr("cx", d => xScale(d["  "]))
            .attr("cy", d => yScale(d[" "]))
            .attr("r", d => rScale(d[" "]))
            .attr("fill", d => colorScale(d["  "]))
            .attr("opacity", 0.8)
            .on("mouseover", (event, d) => {
                tooltip.transition().duration(200).style("opacity",1);
                tooltip.html(`
          <strong>${d[""]}</strong><br/>
            : ${d["  "]}<br/>
           : ${d[" "]}<br/>
           : ${d[" "]}<br/>
        `)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 30) + "px");
            })
            .on("mouseout", () => {
                tooltip.transition().duration(200).style("opacity",0);
            });

        // X
        svg.append("g")
            .attr("transform", `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .style("font-size","12px");

        // Y
        svg.append("g")
            .attr("transform", `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale))
            .style("font-size","12px");

        // X
        svg.append("text")
            .attr("x", svgWidth/2)
            .attr("y", svgHeight - 25)
            .attr("text-anchor","middle")
            .style("font-size","14px")
            .text("  ");

        // Y
        svg.append("text")
            .attr("transform","rotate(-90)")
            .attr("x", -(svgHeight/2))
            .attr("y", margin.left - 50)
            .attr("text-anchor","middle")
            .style("font-size","14px")
            .text(" ");

        // 
        svg.append("text")
            .attr("x", svgWidth/2)
            .attr("y", margin.top/2)
            .attr("text-anchor","middle")
            .style("font-size","16px")
            .text("Bubble Chart: Tour Size vs. Satisfaction vs. Responders");

    }, [data]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default TourBubbleChart;
